import React from 'react';
import { Redirect, Route, Switch, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';

import asyncComponent from "./components/layout/AsyncComponent";
const BaseContainer = asyncComponent(() => import("./components/layout/base/BaseContainer"));
const LoginContainer = asyncComponent(() => import("./components/login/LoginContainer"));
const MainContainer = asyncComponent(() => import("./components/main/MainContainer"));

const listofPages = [
    '/login',
    '/clients'
];

const mapStateToProps = (state) => {
    return {
        oauth : state.login,
        location : state.router.location
    }
};

const Routes = ({ location, oauth }) => {

    if (listofPages.indexOf(location.pathname) > -1) {
        return (
            // Unlogged components
            <Switch location={location}>
                <Route path='/login' component={LoginContainer} />
                {/* <Route path='/clients' component={LoginContainer} /> */}
                <Redirect to="/login" />
            </Switch>
        )
    }
    else {
        return (
            // Private components
            oauth.loggedIn ? 
            <BaseContainer>            
                <div className="container">
                    <Switch location={location}>
                        <Route path="/" component={MainContainer} />
                        {/* <Route path="/submenu" component={SubMenu} /> */}
                        {/* <Redirect to="/" /> */}
                    </Switch>
                </div>
            </BaseContainer>

            : <Redirect push to={{ pathname: '/login', state: { from: location } }} />
        )
    }
}

export default connect(mapStateToProps)(Routes);


