import loginConstants from '../constants/LoginConstants';

let session = JSON.parse(localStorage.getItem('session'));
const initialState = session ? { loggedIn: true, session } : { loggedIn: false };

export function login(state = { ...initialState, isSubmitting: false }, action) {
  switch (action.type) {
    case loginConstants.LOGIN_REQUEST:
      return Object.assign({}, state, {
        isSubmitting: true
      })
    case loginConstants.LOGIN_SUCCESS:
      return Object.assign({}, state, {
        loggedIn: true,
        session: action.session,
        isSubmitting: false
      })
    case loginConstants.LOGIN_FAILURE:
      return Object.assign({}, state, {
        isSubmitting: false
      })
    case loginConstants.LOGIN_LOGOUT:
      return Object.assign({}, state, {
        loggedIn: false,
        session: null
      })
    default:
      return state
  }
}

export function register(state = { isSubmitting: false }, action) {
  switch (action.type) {
    case loginConstants.REGISTER_REQUEST:
      return Object.assign({}, state, {
        isSubmitting: true
      })
    case loginConstants.REGISTER_SUCCESS:
      return Object.assign({}, state, {
        isSubmitting: false
      })
    case loginConstants.REGISTER_FAILURE:
      return Object.assign({}, state, {
        isSubmitting: false
      })
    default:
      return state
  }
}






